@charset "UTF-8";
@import url(components_mok.css);
/* s : reset */
html, body {
	height: 100%;
}

body {
	font-size: 14px;
	line-height: 1.4;
	font-family: "Roboto", "Noto Sans KR", "PingFang SC", "Apple SD Gothic Neo", "Microsoft YaHei UI", "Malgun Gothic", sans-serif;
	color: #000;
}

table, th, td, input, select, button, textarea, pre {
	font: inherit;
	color: inherit;
}

body, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, fieldset, input, select, button, textarea, p, blockquote, table, th, td {
	margin: 0;
	padding: 0;
}

h1, h2, h3, h4, h5, h6 {
	font-size: 100%;
}

ol, ul {
	list-style: none;
}

img, fieldset {
	border: 0;
}

img {
	vertical-align: middle;
}

a {
	color: #000;
	text-decoration: none;
}

table {
	border-spacing: 0;
	border-collapse: collapse;
	border: 0;
}

th, td {
	word-break: break-all;
	word-wrap: break-word;
}

label {
	cursor: pointer;
}

input, button {
	border: 0;
	border-radius: 0;
	background: none;
}

textarea {
	resize: none;
}

input, button, select, textarea {
	vertical-align: middle;
}

address, caption, cite, code, dfn, em, var {
	font-style: normal;
}

hr {
	clear: both;
	display: none;
}

blockquote, q {
	quotes: none;
}

abbr, acronym {
	border: 0;
}

legend, caption {
	visibility: hidden;
	overflow: hidden;
	line-height: 0;
	width: 0;
	height: 0;
	margin: 0;
	padding: 0;
	text-indent: -99999px;
	font-size: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
	display: block;
}

.hidden {
	position: absolute;
	visibility: hidden;
	overflow: hidden;
	height: 0;
	line-height: 0;
	width: 0;
	margin: 0;
	padding: 0;
	font: 0/0 Arial;
}

/* e : reset */
.skip_nav_wrap {
	position: absolute;
	font-size: 0;
	left: 0;
	right: 0;
	top: 0;
	z-index: 100;
}
.skip_nav_wrap a {
	position: absolute;
	display: inline-block;
	color: #fff;
	width: 100%;
	padding: 10px;
	background: #000;
	text-align: center;
	font-size: 15px;
	left: 0;
	right: 0;
	top: 0;
